@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
:root {
  /* ## Layout */

/* The designs were created to the following widths: */

--Mobile: 375px;
--Desktop: 1440px;


/* ## Colors

### Primary */

--Red: hsl(1, 90%, 64%);
--Blue: hsl(219, 85%, 26%);

/* ### Neutral */

--White: hsl(0, 0%, 100%);
--Very-light-grayish-blue: hsl(210, 60%, 98%);
--Light-grayish-blue-1: hsl(211, 68%, 94%);
--Light-grayish-blue-2: hsl(205, 33%, 90%);
--Grayish-blue: hsl(219, 14%, 63%);
--Dark-grayish-blue: hsl(219, 12%, 42%);
--Very-dark-blue: hsl(224, 21%, 14%);

/* ## Typography

### Body Copy */

--paragraph: 10px;
}

body, html {
  margin: 0;
  padding: 0;
  font-size: var(--paragraph);
  font-family: "Plus Jakarta Sans", sans-serif;
  width: 100%;
  height: 100%;
}
#root {
  position: fixed;
  left: 0;
  top: 0;
  background-color: var(--Very-light-grayish-blue);
  height: 100%;
  width: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* main */
main {
  width: var(--Mobile);
  
}
/* nav */
nav {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.notification p {
  font-weight: bolder;
  letter-spacing: 1px;
}
.unread-num {
  margin-left: 5px;
  color: var(--White);
  background-color: var(--Blue);
  padding: 2px 5px;
  border-radius: 5px;
  text-align: center;
}
.mark-all button {
  border: 0;
  color: var(--Dark-grayish-blue);
  background-color: var(--Very-light-grayish-blue);
  font-weight: 500;
  cursor: pointer;
}
.mark-all button:hover {
  color: var(--Blue);
  font-size: 15px;
}
/* list */
ul {
  list-style-type: none;
  margin-top: 30px;
}
ul li {
  display: flex;
  column-gap: 20px;
  margin: 15px 0;
  background-color: var(--Light-grayish-blue-1);
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.read {
  background-color: white;
}
.list-container {
  display: flex;
  justify-content: space-between;
}
/* p width */
.content p {
  width: 200px;
}
.content {
  margin: 0 40px 0 20px;
}

.img, .content {
  vertical-align: middle;
}
.img img {
  width: 30px;
}
.name {
    color: black;
    font-weight: 800;
}
.name:hover {
    color: var(--Blue);
    font-size: 12px;
}
.post {
    color: rgba(43, 37, 37, 0.7);
    font-weight: 800;
}
.post:hover {
  color: var(--Very-dark-blue);
  font-size: 12px;
}
.time {
    color: rgba(43, 37, 37, 0.5);
}
.club {
  color: var(--Blue);
  font-weight: 800;
}
.club:hover {
  color: var(--Very-dark-blue);
  font-size: 12px;
}
.rizky {
  position: relative;

}
.private-message {
  width: 300px;
  background-color: white;
  border: 1px solid rgba(43, 37, 37, 0.5);
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
}
.private-message:hover {
    background-color: var(--Light-grayish-blue-2);
}
.comment {
  display: flex;
  justify-content: space-between;
  max-width: 800px;
  width: 100%;
}
.img-post img {
  width: 35px;
  vertical-align: middle;
  
}
.circle-icon {
  color: var(--Red);
  font-size: x-small;
  margin-left: 6px;
}
.liked-img {
  width: 40px;
  margin-left: 100px;
  vertical-align: middle;
}
footer {
  display: flex;
  justify-content: center;
}
footer p {
  text-align: center;
}

@media only screen and (min-width: 1000px) {
  main {
    width: 600px;
  }
  .content {
    margin: 0 20px 0 20px;
  }
  .content p {
    width: 300px;
  }
}

